@use 'bases/variables' as v; 
@use 'bases/mixins' as m;

/* formulario 
--------------------------------------------------------------------------*/
.formulario{
    margin-bottom: v.$separacion;
    @include m.tablet{ 
        @include m.grid(2, v.$separacion);
    }
    &__contenido{
        background-color: v.$primario;
        margin-bottom: v.$separacion;
        padding: 5rem 3rem;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        @include m.desktop{ 
            padding: v.$separacion;
            margin-bottom: 0;
        }
    }
    &__heading{
        color: v.$blanco;
        text-align: center;
    }
    &__campo{
        margin-bottom: 1rem;
    }
    &__label{
        display: block;
        text-transform: uppercase;
        color: v.$blanco;
        margin: 1rem 0;
    }
    &__input{
        width: 100%;
        padding: 1.5rem;
        border: none;
    }
    &__boton{
        margin-top: 2rem;
        @include m.boton(v.$secundario, v.$blanco);
        @include m.tablet{ 
            width: 100%;
        }
    }
    &__imagen-contenedor{
        display: flex;
        justify-content: center;
    }
    &__imagen{
        width: 30rem;
        @include m.desktop{ 
            width: 100%;
        }
    }

}
